export default {
	data() {
		return {
			fileDataList: [
				{
					"size":"326119",             
					"createTime":"1539326270",       
					"fileName":"aaa",
					"filePath":"1"		
				},{
					"size":"326119",             
					"createTime":"1539326270",       
					"fileName":"aaa",
					"filePath":"1"		
				},{
					"size":"326119",             
					"createTime":"1539326270",       
					"fileName":"aaa",
					"filePath":"1"		
				}
			]
		}
	},
	methods: {
		checkLocalFile(){
			console.log(this.fileDataList);
			uni.getSavedFileList({
			  success: (res)=> {
				  console.log(JSON.stringify(res))
				this.fileDataList = res.fileList;
				console.log(this.fileDataList)
				for(let f of res.fileList){
					let nameAttr = f.filePath.split("/");
					f.fileName = nameAttr[nameAttr.length-1];
					// f.createTime = new Date(f.createTime).toLocaleString();
					uni.getStorage({
						key:f.fileName,
						success:(res)=>{
							console.log("对比成功")
							f.fileName = res.data;
						}
					});
				}
				
			  }
			});
			
		},
		fileOpen(fileName) {
			let paName = "";
			uni.getStorage({
				key:fileName,
				success:(res)=>{
					console.log("对比成功")
					paName = res.data;
				}
			});
			
			uni.openDocument({
				filePath:"_doc/uniapp_temp/download/uniapp_save/"+paName,
				//_doc/uniapp_save/1539245941096.pdf
				success:function(result){
					console.log(JSON.stringify(result))
					
					console.log("打开成功");
				},
				fail:function(res){
					console.log(JSON.stringify(res))
				}
			})
		},
	},
	onLoad(option) {
		this.testToken = server.net.getTestToken();
		console.log("测试token："+server.net.getTestToken());
		//获取文件列表
		console.log(this.fileDataList)
		this.checkLocalFile();
	},onPullDownRefresh() {
		console.log('下拉刷新');
		
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 1000);
	}
}



